import uCharts from './u-charts/u-charts.js';
import vSwiper from './v-swiper.vue';
let _self;
let canvaMap = null;
export default {
	components: {
		vSwiper
	},
	data() {
		return {
			cardCur: 0,
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			textarea: '',
			modalName: null, //dialog 弹出层
			loadiing: false,
			hbDotTitle: [{ title: '新增确诊' }, { title: '确诊' }, { title: '死亡/治愈' }, { title: '病死率' }, { title: '治愈率' }],
			qunguoDotTitle: [{ title: '新增疑似/新增确诊' }, { title: '确诊/疑似' }, { title: '死亡/治愈' }, { title: '病死率/治愈率' }],
			quanguoTrendChart: [], //全国的疫情走势图
			hbFeiHbTrendChart: [], //湖北的疫情走势图
			dataed: [
				{ bgColor: 'bg-red', title: '确诊病例', num: 37254, num_sub: 2657 },
				{ bgColor: 'bg-yellow', title: '疑似人数', num: 37254, num_sub: 2657 },
				{ bgColor: 'bg-grey', title: '死亡人数', num: 37254, num_sub: 2657 },
				{ bgColor: 'bg-green', title: '治愈人数', num: 37254, num_sub: 2657 }
			],
			areaTableData: [], //数据
			areaTitle: [
				{ name: '地区', bgColor: 'bg-cyan' },
				{ name: '现存确诊', bgColor: 'bg-red' },
				{ name: '累计确诊', bgColor: 'bg-pink' },
				{ name: '死亡', bgColor: 'bg-grey' },
				{ name: '治愈', bgColor: 'bg-green' }
			]
		};
	},
	created() {
		this.getData();
		this.getTableData();
		_self = this;











		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(750);
		this.getServerData();
	},
	methods: {
		getData() {
			uni.request({
				method: 'GET',
				url: 'https://ncov.zhouxuanyu.com/Statistics',
				success: res => {
					setTimeout(() => {
						this.loadiing = true;
					});
					this.dataed[0].num = res.data.confirmedCount; //现确诊数
					this.dataed[0].num_sub = res.data.confirmedIncr; //较比昨天增长数
					this.dataed[1].num = res.data.suspectedCount; //疑诊数
					this.dataed[1].num_sub = res.data.suspectedIncr; //较比昨天疑诊增加数
					this.dataed[2].num = res.data.deadCount; //死亡数
					this.dataed[2].num_sub = res.data.deadIncr; //较昨天死亡增加数
					this.dataed[3].num = res.data.curedCount; //治愈人数
					this.dataed[3].num_sub = res.data.curedIncr; //较比昨天治愈人数增长数
					// 走势图
					this.quanguoTrendChart = res.data.quanguoTrendChart; //全国疫情走势图
					this.hbFeiHbTrendChart = res.data.hbFeiHbTrendChart; //湖北疫情走势图
				}
			});
		},
		// 显示数据说明的弹出层
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target;
		},
		// 关闭数据说明的弹出层
		hideModal() {
			this.modalName = null;
		},
		handleShow(index) {
			this.areaTableData[index].isShowCities = !this.areaTableData[index].isShowCities;
		},
		// 请求获取疫情地区表单数据
		getTableData() {
			uni.request({
				method: 'GET',
				url: 'https://ncov.zhouxuanyu.com/cities',
				success: res => {
					res.data.newslist.map(item => {
						item.isShowCities = false;
						return item;
					});
					this.areaTableData = res.data.newslist;
				}
			});
		},
		// 获取疫情地球数据
		async getServerData() {
			let cMap = { series: [] };
			await uni.request({
				url: 'https://www.zhouxuanyu.com/map.json',
				dataType: 'json',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				success: function(res) {
					cMap.series = res.data.features;
					uni.request({
						method: 'GET',
						url:"https://ncov.zhouxuanyu.com/cities",
						dataType: 'json',
						success: res => {
							let datas = res.data.newslist;
							let series = cMap.series.map(province => {
								for (var i = 0; i < datas.length; i++) {
									if (datas[i].provinceName === province.properties.name) {
										return { ...province, ...datas[i] };
									}
								}
								return province;
							});
							cMap.series = series;
							_self.$nextTick(() => {
								_self.showMap('canvasMap', cMap);
							});
						},
						fail: () => {
							_self.tips = '网络错误，小程序端请检查合法域名';
						}
					});
				},
				fail: () => {
					_self.tips = '网络错误，小程序端请检查合法域名';
				}
			});
		},
		showMap(canvasId, chartData) {
			console.log('ucharts初始化成功');
			canvaMap = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'map',
				fontSize: 10,
				padding: [0, 0, 0, 0],
				legend: {
					show: false
				},
				// colors: ['#f59e83','#ffffff', '#4f070d', '#e55a4e', '#fdebcf', '#cb2a2f'],
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				dataLabel: true,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					map: {
						border: true,
						borderWidth: 1,
						borderColor: '#666666',
						fillOpacity: 1
					}
				},
				enableMarkLine: true,
				animation: true
			});
		},
		touchMap(e) {
			console.log(canvaMap);
			canvaMap.showToolTip(e, {
				format: function(item) {
					return `${item.properties.name}: 确诊${item.confirmedCount}人`;
				}
			});
		}
	}
};
